@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,600;1,400&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@1,600&display=swap');

$grey: #D6DBE3;
$black: #111;
$white: #fff;
$beige: #FAEBD7;
$violet: #861657;

* {
    padding: 0;
    margin: 0;
}
 a {
    text-decoration: none;
    color: $black;
    transition: color 500ms ease-out 500ms;
}

a:hover{
    text-decoration: underline;
    color: $violet;
    transition: color 500ms ease-out 500ms;
}
a:active,
a:focus {
    color: $violet;
}
a:visited {
    color: blue;
}
body {
    color: $black;
    font-family: 'Montserrat';
    font-weight: normal;
}

p {
    font-size: 16px;
    line-height: 150%;
}

ul {
    margin-left: 16px;
    li {
        font-size: 16px;
        line-height: 150%;
        margin-top: 10px;
    }
}

.wrapper {
    padding: 5% 10%;
}

header,
footer {
    background: $grey;
    padding: 3% 3% 0;
}

header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border: 1px solid $black;
    border-bottom:  none;
}

.info {
    width: 60%;
}

.info .name {
    font-size: 72px;
    font-weight: bold;
    font-family: 'Playfair Display';
    color: $violet;
    line-height: 100%;
}

.info .profession {
    font-size: 30px;
    margin-top: 15px;
    font-weight: normal;
}

.info .contacts {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin: 30px 0;
}
.info .contacts a {
    font-size: 20px;
    display: flex;
    margin: 0 15px 15px 0;
    img {
        margin-right: 5px;
        width: 24px;
        height: auto;
    }
}


.photo {
    width: 30%;
}
.photo img{
width: 100%;
height: auto;
}

main {
    background: $grey;
    border-right: 1px solid $black;
    border-left: 1px solid $black;
}

.main-wrapper {
    display: flex;
    justify-content: space-between;
    
    
    
    h4 {
        font-size: 30px;
        color: $violet;
        font-family: 'Playfair Display';
        
    }
    hr {
        margin-bottom: 20px;
    }
    .item-block {
        margin-top: 50px;
         &:nth-child(1) {
        margin-top: 0px;
        
    }
    }
   
    
    .left-block {
        width: 25%;
        background: $white;
        padding: 3%;
        
        h4 {
            margin-bottom: 20px;
        }
    }
    
    .right-block {
        width: 70%;
        padding: 3%;
        background: $beige;
       
        
        table, ul, h4 {
            padding-left: 5%;
        }
        
        tr {
            display: flex;
            align-items: start;
            span {
                font-weight: bold;
                font-size: 16px;
                line-height: 150%;
            
            }
            &:nth-child(2){
                margin-top: 20px;
            }
        }
        
        
        
        td:first-child {
            min-width: 200px;
            margin-right: 15px;
        }
        
    }
}

footer {
    border: 1px solid $black;
    border-top:   none;
}

@media(max-width: 1199px){
    .wrapper {
        padding: 5%;
    }
}
@media(max-width: 767px){
    .wrapper {
        padding: 3%;
    }
    
    .info .name {
        font-size: 48px;
    }
    
    .info .profession {
        font-size: 24px;
    }
    .main-wrapper .left-block, .main-wrapper .right-block {
        width: 94%;
    }
    .main-wrapper .left-block {display: flex;}
    .main-wrapper {
        flex-direction: column;
    }
    .objective {
        width: 55%;
        margin-right: 5%;
    }
    .language.item-block {
        margin-top: 0;
        width: 40%;
    }
    
}

@media(max-width: 577px){
    header {
        align-items: start;
    }
    .info .name {
        font-size: 36px;
    }
    .info .profession {
        font-size: 20px;
    }
    
    .info .contacts a {
        font-size: 18px;
    }
    .main-wrapper .right-block tr, .main-wrapper .left-block {
        flex-direction: column;
    }
    .language.item-block {
        margin: 20px 0;
        width: 100%;
    }
    .objective {
        width: 100%;
        margin-right: 0;
    }
    .main-wrapper .right-block h4, .main-wrapper .right-block table, .main-wrapper .right-block ul {
        padding-left: 0;
    }
    
}
@media(max-width: 400px) {
    .info .profession {
        font-size: 16px;
    }
}